/*global firebase*/
import React ,{Component} from 'react';
import './PopCard.css';
//Action  .
import {userArticles,loadArticles} from './actions/Article'
import firebase from 'firebase'
//redux connect  .
import {connect} from 'react-redux';

class PopCard extends Component{ 
  logout() {
    firebase.auth().signOut();
  }
  //Article Action   userArticles, loadArticles dispatch 
  //.
  render() {
    const {dispatch} = this.props;
    if(this.props.isPop) {
      return(
        <div className="card_pop">
          <div className="card_pop_arrow"/>
          <div>
          <a href="#" onClick={()=>dispatch(userArticles())}>  </a>
          </div>
          <div>
          <a href="#" onClick={()=>dispatch(loadArticles())}>  </a>
          </div>
          <div>
            <a href="#" onClick={()=>this.logout()}></a>
          </div>
        </div>);
    } else {
      return <div/>
    }
  }
}
// redux connect     .
export default connect()(PopCard)


